import { createSlice, PayloadAction } from '@reduxjs/toolkit';

import {
  getBookmarksFilms,
  getFavouritesFilms,
  getFilteredFilms
} from '../../modules/films';
import { getDefaultFiltersState } from '../../modules/filters';
import { IFilmsState } from '../../types/films';
import { IFiltersState } from '../../types/filters';
import { filmsItems } from '../films-items';

const filmsDefaultFilters: IFiltersState = getDefaultFiltersState();

const initialState: IFilmsState = {
  films: getFilteredFilms([...filmsItems], filmsDefaultFilters),
  favorites: [],
  bookmarks: []
};

const filmsSlice = createSlice({
  name: 'films',
  initialState,
  reducers: {
    getFilms(state, action: PayloadAction<IFiltersState>) {
      state.films = getFilteredFilms([...filmsItems], action.payload);
    },
    addToFavorites(state, action: PayloadAction<number[]>) {
      state.favorites.push(...action.payload);
    },
    deleteFromFavorites(state, action: PayloadAction<number>) {
      state.favorites = state.favorites.filter((favoriteId) => favoriteId !== action.payload);
    },
    addToBookmarks(state, action: PayloadAction<number[]>) {
      state.bookmarks.push(...action.payload);
    },
    deleteFromBookmarks(state, action: PayloadAction<number>) {
      state.bookmarks = state.bookmarks.filter((bookmarkId) => bookmarkId !== action.payload);
    },
    resetFavoritesAndBookmarks(state) {
      state.favorites = [];
      state.bookmarks = [];
    },
    getFavorites(state) {
      state.films = getFavouritesFilms([...filmsItems], state.favorites);
    },
    getBookmarks(state) {
      state.films = getBookmarksFilms([...filmsItems], state.bookmarks);
    }
  }
});

export const {
  getFilms,
  addToFavorites,
  deleteFromFavorites,
  addToBookmarks,
  deleteFromBookmarks,
  resetFavoritesAndBookmarks,
  getFavorites,
  getBookmarks
} = filmsSlice.actions;

export default filmsSlice.reducer;
