import { createSlice, PayloadAction } from '@reduxjs/toolkit';

import { getAuthorizedAccount, isAuthorized } from '../../modules/authorization';
import { IAuthorizationState, IUserAccount } from '../../types/authorization';

const initialState: IAuthorizationState = {
  auth: {
    login: '',
    password: '',
    isAuthorized: false
  }
};

const authSlice = createSlice({
  name: 'authorization',
  initialState,
  reducers: {
    login(state, action: PayloadAction<IUserAccount>) {
      state.auth = isAuthorized(action.payload)
        ? getAuthorizedAccount(action.payload)
        : initialState.auth;
    },
    logout(state) {
      state.auth = initialState.auth;
    }
  }
});

export const { login, logout} = authSlice.actions;

export default authSlice.reducer;
