import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import {
  BOOKMARKS_AUTHORIZED_OPTION,
  DEFAULT_AUTHORIZED_OPTION,
  FAVORITES_AUTHORIZED_OPTION
} from '../store/filters';
import { getBookmarks, getFavorites, getFilms } from '../store/slices/filmsSlice';
import { useTypedSelector } from './useTypedSelector';

export const useSelectAuthorizedFilter = (authorizedOption: string): void => {
  const dispatch = useDispatch();
  const filters = useTypedSelector((state) => state.filters);

  useEffect(() => {
    if (authorizedOption === DEFAULT_AUTHORIZED_OPTION) {
      dispatch(getFilms(filters));
      return;
    }

    if (authorizedOption === FAVORITES_AUTHORIZED_OPTION) {
      dispatch(getFavorites());
      return;
    }

    if (authorizedOption === BOOKMARKS_AUTHORIZED_OPTION) {
      dispatch(getBookmarks());
      return;
    }
  }, [authorizedOption]);
};
