import React, { useEffect, useRef, useState } from 'react';
import { useDispatch } from 'react-redux';

import { useModalClose } from '../../hooks/useModalClose';
import { AUTH_ERROR_MESSAGES, isAuthorized, validateAuthForm } from '../../modules/authorization';
import storage from '../../modules/localstorage';
import { login } from '../../store/slices/authSlice';

import classes from './index.module.scss';

const INIT_ERROR_MESSAGES: string[] = [];
const ERROR_DISPLAY_TIME = 4000;
const ERROR_INPUT_STYLE = '2px solid #e33618';

const AuthForm: React.FC = () => {
  const [errorMessages, setErrorMessages] = useState(INIT_ERROR_MESSAGES);
  const dispatch = useDispatch();
  const closeModal = useModalClose();
  const loginElement = useRef<HTMLInputElement>(null!);
  const passwordElement = useRef<HTMLInputElement>(null!);

  useEffect(() => {
    loginElement.current.focus();
  }, []);

  const deleteErrorStyles = (): void => {
    loginElement.current.style.outline = '';
    passwordElement.current.style.outline = '';
  };

  const setErrorStyles = (errors: string[]): void => {
    if (errors.includes(AUTH_ERROR_MESSAGES.emptyLogin)) {
      loginElement.current.style.outline = ERROR_INPUT_STYLE;
    }

    if (errors.includes(AUTH_ERROR_MESSAGES.emptyPassword)) {
      passwordElement.current.style.outline = ERROR_INPUT_STYLE;
    }
  };

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = (e) => {
    e.preventDefault();

    const account = {
      login: loginElement.current.value,
      password: passwordElement.current.value,
    };

    const errors = validateAuthForm(account);

    if (errors && errors.length) {
      setErrorMessages(errors);
      setErrorStyles(errors);
      return;
    }

    if (isAuthorized(account)) {
      storage.saveAccountData(account);
      dispatch(login(account));
      closeModal();
      return;
    }

    setErrorMessages([AUTH_ERROR_MESSAGES.wrongData]);
  };

  const deleteErrorMessage = (timer: NodeJS.Timeout): void => {
    setErrorMessages(INIT_ERROR_MESSAGES);
    deleteErrorStyles();
    clearTimeout(timer);
  };

  if (errorMessages && errorMessages.length) {
    const errorTimer = setTimeout(() => {
      deleteErrorMessage(errorTimer);
    }, ERROR_DISPLAY_TIME);
  }

  return (
    <form className={classes.form} onSubmit={handleSubmit}>
      <h2 className={classes.title}>Войти в аккаунт</h2>
      <p>
        <label>
          <input className={classes.textInput} type="text" placeholder="Логин" ref={loginElement} />
        </label>
      </p>
      <p>
        <label>
          <input
            className={classes.textInput}
            type="password"
            placeholder="Пароль"
            ref={passwordElement}
          />
        </label>
      </p>

      <button className={classes.submit} type="submit">
        Подтвердить
      </button>

      {!!errorMessages.length && (
        <div className={classes.errorMessageList}>
          {errorMessages.map((message) => (
            <p key={message} className={classes.errorMessage}>
              {message}
            </p>
          ))}
        </div>
      )}
    </form>
  );
};

export default AuthForm;
