import Radio from '../Radio';
import classes from './index.module.scss';

interface ITogglerProps {
  title?: string;
  name: string;
  togglerValue: string;
  firstLabel: string;
  firstId: string;
  firstValue: string;
  secondLabel: string;
  secondId: string;
  secondValue: string;
  onChange: (value: string) => void;
}

const Toggler: React.FC<ITogglerProps> = (props) => {
  const {
    title,
    name,
    togglerValue,
    firstLabel,
    firstId,
    firstValue,
    secondLabel,
    secondId,
    secondValue,
    onChange
  } = props;

  const togglerClassName = togglerValue === firstValue
  ? `${classes.innerWrapper} ${classes.isActive}`
  : `${classes.innerWrapper}`;

  return (
    <>
      {
        !!title &&
        <p className={classes.title}>{title}</p>
      }

      <div className={classes.outerWrapper}>
        <label className={classes.firstLabel} htmlFor={firstId}>
          {firstLabel}
        </label>
        <div className={togglerClassName}>
          <Radio
            className={classes.firstRadio}
            id={firstId}
            name={name}
            value={firstValue}
            isChecked={firstValue === togglerValue}
            onChange={onChange}
          />
          <Radio
            className={classes.secondRadio}
            id={secondId}
            name={name}
            value={secondValue}
            isChecked={secondValue === togglerValue}
            onChange={onChange}
          />
        </div>
        <label className={classes.secondLabel} htmlFor={secondId}>
          {secondLabel}
        </label>
      </div>
    </>
  );
};

export default Toggler;
