const padZero = (string: string): string => {
  if (!string.length) {
    return '';
  }

  return string.length === 1 ? `0${string}` : string;
};

const formatDate = (date: string | Date): string => {
  let formattedDate: string | Date = date;

  if (!formattedDate) {
    return '';
  }

  if (typeof formattedDate === 'string') {
    formattedDate = new Date(Date.parse(formattedDate));
  }

  const day = String(formattedDate.getDate());
  const month = String(formattedDate.getMonth() + 1);
  const year = String(formattedDate.getFullYear());

  return `${padZero(day)}.${padZero(month)}.${year}`;
};

export { formatDate };
