import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Button, Stack } from '@mui/material';

import { useTypedSelector } from '../../hooks/useTypedSelector';
import { ITEMS_PER_PAGE } from '../../store/parameters';
import { getPagesCount } from '../../modules/pagination';
import {
  decrementPage,
  getFirstPage,
  incrementPage
} from '../../store/slices/paginationSlice';

import classes from './index.module.scss';

const Pagination: React.FC = () => {
  const pageNumber = useTypedSelector((state) => state.pagination.page);
  const films = useTypedSelector((state) => state.films.films);
  const dispatch = useDispatch();

  const filmsCount: number = films.length;
  const pagesCount: number = getPagesCount(filmsCount, ITEMS_PER_PAGE);

  useEffect(() => {
    dispatch(getFirstPage());
  }, [films]);

  const handlePrevClick = (): void => {
    if (pageNumber > 1) {
      dispatch(decrementPage());
    }
  };

  const handleNextClick = (): void => {
    if (pageNumber < pagesCount) {
      dispatch(incrementPage());
    }
  };

  if (!filmsCount) {
    return null;
  }

  return (
    <>
      {
      pagesCount > 1 &&
        <Stack
          direction="row"
          spacing={2}
          sx={{width: "100%", mb: "20px"}}
        >
          <Button
            variant="contained"
            color="primary"
            sx={{borderRadius: "4px 0 0 4px", textTransform: "none", flexGrow: 1}}
            disabled={pageNumber === 1}
            onClick={handlePrevClick}
          >
            Назад
          </Button>
          <Button
            variant="contained"
            color="primary"
            sx={{borderRadius: "0 4px 4px 0", textTransform: "none",  flexGrow: 1}}
            disabled={pageNumber === pagesCount}
            onClick={handleNextClick}
          >
            Вперёд
          </Button>
        </Stack>
      }

      <div className={classes.paginationPages}>
        <span>{pageNumber}</span>
        {` из `}
        <span>{pagesCount}</span>
      </div>
    </>
  );
};

export default Pagination;
