import {
  FilmsAction,
  FilmsActionTypes,
} from "../../types/films";
import { IFiltersState } from "../../types/filters";

export const getFilmsAction = (filter: IFiltersState): FilmsAction => (
  {
    type: FilmsActionTypes.GET_FILMS,
    filter
  }
);

export const addToFavoritesAction = (filmIds: number[]): FilmsAction => (
  {
    type: FilmsActionTypes.ADD_TO_FAVORITES,
    filmIds
  }
);

export const deleteFromFavoritesAction = (filmId: number): FilmsAction => (
  {
    type: FilmsActionTypes.DELETE_FROM_FAVORITES,
    filmId
  }
);

export const addToBookmarksAction = (filmIds: number[]): FilmsAction => (
  {
    type: FilmsActionTypes.ADD_TO_BOOKMARKS,
    filmIds
  }
);

export const deleteFromBookmarksAction = (filmId: number): FilmsAction => (
  {
    type: FilmsActionTypes.DELETE_FROM_BOOKMARKS,
    filmId
  }
);

export const resetFavoritesAndBookmarksAction = (): FilmsAction => (
  {
    type: FilmsActionTypes.RESET_FAVORITES_AND_BOOKMARKS
  }
);

export const getFavoritesAction = (): FilmsAction => (
  {
    type: FilmsActionTypes.GET_FAVORITES
  }
);

export const getBookmarksAction = (): FilmsAction => (
  {
    type: FilmsActionTypes.GET_BOOKMARKS
  }
);
