import { getGenresByIds, getPosterUrl } from '../../modules/films';
import { IFilmItem } from '../../types/films';
import classes from './index.module.scss';

interface IFoundFilmProps {
  film: IFilmItem | null;
}

const FoundFilm: React.FC<IFoundFilmProps> = (props) => {
  const {film} = props;

  if (!film) {
    return (
      <h2 className={classes.notFoundTitle}>
        Не найдено фильмов по этим критериям
      </h2>
    );
  }

  const genres = getGenresByIds(film.genre_ids);

  return (
    <article className={classes.filmPage}>
      <div className={classes.imageContainer}>
        <img
          src={`${getPosterUrl( film.poster_path || film.backdrop_path)}`}
          alt={`${film.title} - постер загружается только при включенном VPN`}
        />
      </div>
      <div className={classes.description}>
        <h2 className={classes.title}>{film.title}</h2>
        <div className={classes.rating}>
          <span className={classes.genresTitle}>Жанр:</span>{' '}
          {
            genres.map((genre) => (
              <span className={classes.genre} key={genre.id}>{genre.name}</span>
            ))
          }
        </div>
        <p className={classes.descriptionText}>
          {film.overview}
        </p>
      </div>
    </article>
  );
};

export default FoundFilm;
