import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Outlet } from 'react-router-dom';
import { useTypedSelector } from '../../hooks/useTypedSelector';

import storage from '../../modules/localstorage';
import {
  addToBookmarksAction,
  addToFavoritesAction
} from '../../store/action-creators/films';
import { login } from '../../store/slices/authSlice';
import {
  IAuthorizedFilmsLists,
  IUserAccount
} from '../../types/authorization';
import AuthForm from '../AuthForm';
import Header from '../Header';
import Modal from '../Modal';

import classes from './index.module.scss';

const Layout: React.FC = () => {
  const {isModalOpen} = useTypedSelector((state) => state.modal);
  const authData = useTypedSelector((state) => state.authorization.auth);
  const {isAuthorized} = authData;
  const dispatch = useDispatch();

  useEffect(() => {
    const userAccount: IUserAccount | null = storage.getAccountData();

    if (userAccount) {
      dispatch(login(userAccount));
    }
  }, []);

  useEffect(() => {
    if (isAuthorized) {
      const filmsLists: IAuthorizedFilmsLists | null = storage.getFilmsLists();

      if (filmsLists) {
        dispatch(addToFavoritesAction(filmsLists.favorites));
        dispatch(addToBookmarksAction(filmsLists.bookmarks));
      }

      return;
    }
  }, [isAuthorized]);

  return (
    <>
      <div className={classes.app}>
        <Header />

        <main className={classes.main}>
          <h1 className={classes.mainTitle}>Online Кинотеатр.</h1>

          <Outlet />
        </main>
      </div>

      {
        isModalOpen &&
        <Modal>
          <AuthForm />
        </Modal>
      }
    </>
  );
};

export default Layout;
