import {
  IAuthorizedFilmsLists,
  IUserAccount
} from '../types/authorization';

enum StorageNames {
  ACCOUNT_STORAGE_NAME = 'account',
  FILMS_LISTS_STORAGE_NAME = 'filmsLists'
}

type StorageData = IUserAccount | IAuthorizedFilmsLists | null;

interface IStorage {
  saveAccountData: (authData: IUserAccount) => void;
  getAccountData: () => IUserAccount | null;
  deleteAccountData: () => void;
  saveFilmsLists: (lists: IAuthorizedFilmsLists) => void;
  getFilmsLists: () => IAuthorizedFilmsLists | null;
}

const saveToStorage = (storageName: string, data: StorageData): void => {
  if (data) {
    localStorage.setItem(storageName, JSON.stringify(data));
  }
};

function getFromStorage (storageName: StorageNames.ACCOUNT_STORAGE_NAME): IUserAccount | null;

function getFromStorage (storageName: StorageNames.FILMS_LISTS_STORAGE_NAME): IAuthorizedFilmsLists | null;

function getFromStorage (storageName: string) {
  const data: string | null = localStorage.getItem(storageName);

  if (data && data.length) {
    return JSON.parse(data);
  }

  return null;
}

const deleteFromStorage = (storageName: string): void => {
  localStorage.removeItem(storageName);
};

const storage: IStorage = {
  saveAccountData (authData) {
    saveToStorage(StorageNames.ACCOUNT_STORAGE_NAME, authData);
  },

  getAccountData () {
    return getFromStorage(StorageNames.ACCOUNT_STORAGE_NAME);
  },

  deleteAccountData () {
    deleteFromStorage(StorageNames.ACCOUNT_STORAGE_NAME);
  },

  saveFilmsLists (filmsLists: IAuthorizedFilmsLists) {
    saveToStorage(StorageNames.FILMS_LISTS_STORAGE_NAME, filmsLists);
  },

  getFilmsLists () {
    return getFromStorage(StorageNames.FILMS_LISTS_STORAGE_NAME);
  }
};

export default storage;
