import React, { useState } from 'react';
import {
  Button,
  Checkbox,
  FormControlLabel,
  FormGroup
} from '@mui/material';

import { useSelectAuthorizedFilter } from '../../hooks/useSelectAuthorizedFilter';
import { useSelectFilters } from '../../hooks/useSelectFilters';
import { useTypedSelector } from '../../hooks/useTypedSelector';

import { getDefaultCheckboxesList } from '../../modules/filters';
import { filmsGenres } from '../../store/films-genres';
import {
  sortingSelectValues,
  DEFAULT_SORTING_OPTION,
  yearSelectValues,
  DEFAULT_YEAR_OPTION,
  SORTING_SELECT_TITLE,
  YEAR_SELECT_TITLE,
  DEFAULT_GENRES_FILTER_VALUE,
  AUTHORIZED_SELECT_TITLE,
  authorizedSelectValues,
  DEFAULT_AUTHORIZED_OPTION,
  FAVORITES_AUTHORIZED_OPTION,
  BOOKMARKS_AUTHORIZED_OPTION
} from '../../store/filters';
import Select from '../UI/Select';

import classes from './index.module.scss';

const initialCheckboxesListState = getDefaultCheckboxesList(filmsGenres);

const Filters: React.FC = () => {
  const [sortingOption, setSortingOption] = useState(DEFAULT_SORTING_OPTION);
  const [yearOption, setYearOption] = useState(DEFAULT_YEAR_OPTION);
  const [genresFilter, setGenresFilter] = useState(DEFAULT_GENRES_FILTER_VALUE);
  const [genresChecboxes, setGenresChecboxes] = useState(initialCheckboxesListState);
  const [authorizedOption, setAuthorizedOption] = useState(DEFAULT_AUTHORIZED_OPTION);

  const authData = useTypedSelector((state) => state.authorization.auth);
  const {isAuthorized} = authData;

  useSelectFilters({sortingOption, yearOption, genresFilter});
  useSelectAuthorizedFilter(authorizedOption);

  const handleGenreFilterChange:
    React.ChangeEventHandler<HTMLInputElement> =
    (e) => {
      const isChecked = e.target.checked;
      const value = Number(e.target.value);
      const updatedCheckboxes = [...genresChecboxes].map((checkbox) => ({
        ...checkbox,
        checked: checkbox.id === value ? isChecked : checkbox.checked
      }));

      setGenresChecboxes(updatedCheckboxes);
      if (isChecked) {
        setGenresFilter([...genresFilter, value]);
        return;
      }

      setGenresFilter(
        [...genresFilter].filter((genre) => genre !== value)
      );
    };

  const resetFilters = (): void => {
    setGenresChecboxes(initialCheckboxesListState);
    setYearOption(DEFAULT_YEAR_OPTION);
    setGenresFilter(DEFAULT_GENRES_FILTER_VALUE);
    setSortingOption(DEFAULT_SORTING_OPTION);
  };

  const handleResetFiltersClick:
    React.MouseEventHandler<HTMLButtonElement> =
    (e) => {
      e.preventDefault();
      resetFilters();
    };

  const isInAuthorizedOption: boolean =
    authorizedOption === FAVORITES_AUTHORIZED_OPTION
    || authorizedOption === BOOKMARKS_AUTHORIZED_OPTION;

  return (
    <>
      <form>
        <Button
          variant="outlined"
          color="primary"
          size="medium"
          type="reset"
          sx={{
            textTransform: "none",
            width: "100%",
            mb: "20px",
            borderWidth: "2px",
            '&:hover': {borderWidth: "2px"}
          }}
          onClick={handleResetFiltersClick}
        >
          Сбросить все фильтры
        </Button>

        {
          isAuthorized &&
          <label className={classes.sortingLabel}>
            <Select
              title={AUTHORIZED_SELECT_TITLE}
              value={authorizedOption}
              options={authorizedSelectValues}
              disabled={false}
              onChange={setAuthorizedOption}
            />
          </label>
        }

        <label className={classes.sortingLabel}>
          <Select
            title={SORTING_SELECT_TITLE}
            value={sortingOption}
            options={sortingSelectValues}
            disabled={isInAuthorizedOption}
            onChange={setSortingOption}
          />
        </label>

        <label className={classes.sortingLabel}>
          <Select
            title={YEAR_SELECT_TITLE}
            value={yearOption}
            options={yearSelectValues}
            disabled={isInAuthorizedOption}
            onChange={setYearOption}
          />
        </label>

        <FormGroup aria-label="Жанр" sx={{mb: "10px"}}>
          {
            genresChecboxes.map((genre) => (
              <FormControlLabel
                key={genre.id}
                label={genre.name}
                control={
                  <Checkbox
                    color="default"
                    size="small"
                    value={genre.id}
                    onChange={handleGenreFilterChange}
                    checked={genre.checked}
                    disabled={isInAuthorizedOption}
                  />
                }
              />
            ))
          }
        </FormGroup>
      </form>
    </>
  );
};

export default Filters;
