import { filmsGenres } from '../store/films-genres';
import {
  popularityValues,
  POPULARITY_BENCHMARK,
  scoreValues,
  SCORE_BENCHMARK,
  VOTES_COUNS_BENCHMARK
} from '../store/filters';
import { IMAGE_SERVER_URL } from '../store/parameters';
import { IFilmGenre, IFilmItem } from '../types/films';
import { IFiltersSelectOption, IFiltersState, ISearchFilters } from '../types/filters';

const getPosterUrl = (pictureName: string | null): string | null => {
  if (!pictureName) {
    return null;
  }

  return `${IMAGE_SERVER_URL}${pictureName}`;
};

const filterFilmsByGenres = (films: IFilmItem[], genresFilter: number[]): IFilmItem[] => {
  if (!genresFilter.length) {
    return films;
  }

  return films.filter(
    (film) => film.genre_ids.some(
      (genreId) => genresFilter.includes(genreId)
    )
  );
};

const filterFilmsByGenreId = (films: IFilmItem[], genreId: number | null): IFilmItem[] => {
  if (!genreId) {
    return films;
  }

  return films.filter(
    (film) => film.genre_ids.some((id) => id === genreId)
  );
};

const filterFilmsByYear = (films: IFilmItem[], yearFilter: IFiltersSelectOption): IFilmItem[] => {
  return films.filter((film) => {
    const yearField = film[yearFilter.field];
    if (typeof yearField === 'string' && (yearField as string).length > 0) {
      const filmYear = new Date(Date.parse(yearField));
      return yearFilter.name === String(filmYear.getFullYear());
    }

    return false;
  });
};

const sortFilmsByField = (films: IFilmItem[], field: string, increase: boolean) => {
  return [...films].sort((firstFilm, secondFilm) => increase
    ? Number(firstFilm[field]) - Number(secondFilm[field])
    : Number(secondFilm[field]) - Number(firstFilm[field])
  );
};

const getFilteredFilms =
  (films: IFilmItem[], filmsFilters: IFiltersState): IFilmItem[]  => {
  const {
    sorting,
    yearFilter,
    genresFilter
  } = filmsFilters;

  let filteredFilms: IFilmItem[] = [...films];

  if (yearFilter) {
    filteredFilms = filterFilmsByYear(films, yearFilter);
  }

  if (genresFilter) {
    filteredFilms = filterFilmsByGenres(filteredFilms, genresFilter);
  }

  if (sorting) {
    filteredFilms = sortFilmsByField(filteredFilms, sorting.field, sorting.increase);
  }

  return filteredFilms;
};

const getFavouritesFilms = (films: IFilmItem[], favoriteIds: number[]): IFilmItem[] => (
  films.filter((film) => favoriteIds.includes(film.id))
);

const getBookmarksFilms = (films: IFilmItem[], bookmarkIds: number[]): IFilmItem[] => (
  films.filter((film) => bookmarkIds.includes(film.id))
);

const getFilmById = (films: IFilmItem[], id: number): IFilmItem | undefined => (
  films.find((film) => film.id === id)
);

const getGenresByIds = (ids: number[]): IFilmGenre[] => (
  filmsGenres.filter((genre) => ids.some((id) => id === genre.id))
);

const filterFilmsByPopularity = (films: IFilmItem[] , popularityValue: string): IFilmItem[] => {
  if (popularityValue === popularityValues.high) {
    return films.filter((film) => (
      film.popularity > POPULARITY_BENCHMARK
      && film.vote_count > VOTES_COUNS_BENCHMARK)
    );
  }

  if (popularityValue === popularityValues.low) {
    return films.filter((film) => (
      film.popularity <= POPULARITY_BENCHMARK
      && film.vote_count <= VOTES_COUNS_BENCHMARK)
    );
  }

  return films;
};

const filterFilmsByScore = (films: IFilmItem[] , scoreValue: string): IFilmItem[] => {
  if (scoreValue === scoreValues.high) {
    return films.filter((film) => film.vote_average > SCORE_BENCHMARK);
  }

  if (scoreValue === scoreValues.low) {
    return films.filter((film) => film.vote_average <= SCORE_BENCHMARK);
  }

  return films;
};

const searchFilms = (films: IFilmItem[], searchFilters: ISearchFilters): IFilmItem[] => {
  const {
    genreId,
    popularityValue,
    scoreValue
  } = searchFilters;

  let filtredFilms: IFilmItem[] = [...films];
  filtredFilms = filterFilmsByGenreId(filtredFilms, genreId);
  filtredFilms = filterFilmsByScore(filtredFilms, scoreValue);
  filtredFilms = filterFilmsByPopularity(filtredFilms, popularityValue);

  return filtredFilms;
};

export {
  getPosterUrl,
  sortFilmsByField,
  getFilteredFilms,
  getFavouritesFilms,
  getBookmarksFilms,
  getFilmById,
  getGenresByIds,
  searchFilms
};
