import { IFiltersState } from "./filters";

export interface IFilmItem {
  adult: boolean;
  backdrop_path: string | null;
  genre_ids: number[];
  id: number;
  original_language: string;
  original_title: string;
  overview: string;
  popularity: number;
  poster_path: string | null;
  release_date: string;
  title: string;
  video: boolean;
  vote_average: number;
  vote_count: number;
  [key: string]: boolean | string | number | number[] | null;
}

export interface IFilmGenre {
  id: number;
  name: string;
}

export interface IFilmsState {
  films: IFilmItem[];
  favorites: number[];
  bookmarks: number[];
}

export enum FilmsActionTypes {
  GET_FILMS = 'GET_FILMS',
  ADD_TO_FAVORITES = 'ADD_TO_FAVORITES',
  DELETE_FROM_FAVORITES = 'DELETE_FROM_FAVORITES',
  ADD_TO_BOOKMARKS = 'ADD_TO_BOOKMARKS',
  DELETE_FROM_BOOKMARKS = 'DELETE_FROM_BOOKMARKS',
  RESET_FAVORITES_AND_BOOKMARKS = 'RESET_FAVORITES_AND_BOOKMARKS',
  GET_FAVORITES = 'GET_FAVORITES',
  GET_BOOKMARKS = 'GET_BOOKMARKS'
}

interface IGetFilmsAction {
  type: FilmsActionTypes.GET_FILMS;
  filter: IFiltersState;
}

interface IAddToFavoritesAction {
  type: FilmsActionTypes.ADD_TO_FAVORITES;
  filmIds: number[];
}

interface IDeleteFromFavoritesAction {
  type: FilmsActionTypes.DELETE_FROM_FAVORITES;
  filmId: number;
}

interface IAddToBookmarksAction {
  type: FilmsActionTypes.ADD_TO_BOOKMARKS;
  filmIds: number[]
}

interface IDeleteFromBookmarksAction {
  type: FilmsActionTypes.DELETE_FROM_BOOKMARKS;
  filmId: number
}

interface IResetFavoritesAndBookmarksAction {
  type: FilmsActionTypes.RESET_FAVORITES_AND_BOOKMARKS;
}

interface IGetFavoritesAction {
  type: FilmsActionTypes.GET_FAVORITES;
}

interface IGetBookmarksAction {
  type: FilmsActionTypes.GET_BOOKMARKS;
}

export type FilmsAction =
  IGetFilmsAction
  | IAddToFavoritesAction
  | IDeleteFromFavoritesAction
  | IAddToBookmarksAction
  | IDeleteFromBookmarksAction
  | IResetFavoritesAndBookmarksAction
  | IGetFavoritesAction
  | IGetBookmarksAction;
