import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { useTypedSelector } from '../../hooks/useTypedSelector';
import {
  AppBar,
  Container,
  Toolbar,
  Box,
  Stack,
  Button
}  from '@mui/material';

import storage from '../../modules/localstorage';
import {
  LOGIN_BUTTON_TEXT,
  LOGOUT_BUTTON_TEXT
} from '../../store/parameters';
import { modalOpen } from '../../store/slices/modalSlice';
import { resetFavoritesAndBookmarks } from '../../store/slices/filmsSlice';
import { logout } from '../../store/slices/authSlice';

import classes from './index.module.scss';

const Header: React.FC = () => {
  const authData = useTypedSelector((state) => state.authorization.auth);
  const {isAuthorized} = authData;
  const dispatch = useDispatch();

  const AuthButtonText = isAuthorized ? LOGOUT_BUTTON_TEXT : LOGIN_BUTTON_TEXT;

  const setLogin = (): void => {
    dispatch(modalOpen());
  };

  const setLogout = (): void => {
    dispatch(logout());
    dispatch(resetFavoritesAndBookmarks());
    storage.deleteAccountData();
  };

  const handleAuthButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (isAuthorized) {
      setLogout();
      return;
    }

    setLogin();
  };
  return (
    <AppBar position="static">
      <Container maxWidth="xl">
        <Toolbar disableGutters={false}>
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'flex' } }}>
            <Stack direction="row" spacing={2}>
              <Link className={classes.navLink} to="/">Главная</Link>
              <Link className={classes.navLink} to="/search">Поиск</Link>
            </Stack>
          </Box>

          <Button
            variant="contained"
            color="success"
            size="medium"
            onClick={handleAuthButtonClick}
          >
            {AuthButtonText}
          </Button>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;
