import { useParams }from 'react-router-dom';
import FilmContent from '../../components/FilmContent';

import { getFilmById, getPosterUrl } from '../../modules/films';
import { filmsItems } from '../../store/films-items';

import classes from './index.module.scss';

const FilmItem: React.FC = () => {
  const {id} = useParams();
  const film = getFilmById(filmsItems, Number(id));

  return (
    !film
    ?
    <h2 className={classes.notFoundTitle}>Фильм не найден</h2>
    :
    <article className={classes.filmPage}>
      <div className={classes.filmMain}>
        <div className={classes.filmMainWrapper}>
          <div className={classes.imageContainer}>
          <img
            src={`${getPosterUrl( film.poster_path || film.backdrop_path)}`}
            alt={`${film.title} - постер загружается только при включенном VPN`}
          />
          </div>
          <div className={classes.description}>
            <h2 className={classes.title}>{film.title}</h2>
            <div className={classes.rating}>
              Рейтинг: {film.vote_average}
            </div>
            <p className={classes.descriptionText}>
              {film.overview}
            </p>
          </div>
        </div>
      </div>

      <div className={classes.filmContent}>
        <div className={classes.filmContentWrapper}>
          <FilmContent film={film} />
        </div>
      </div>
    </article>
  );
};

export default FilmItem;
