import { getGenresByIds } from '../../modules/films';
import { formatDate } from '../../modules/utils';
import { IFilmItem } from '../../types/films';

import classes from './index.module.scss';

interface IPropsFilmContent {
  film: IFilmItem;
}

const FilmContent: React.FC<IPropsFilmContent> = (props) => {
  const {film} = props;
  const genres = getGenresByIds(film.genre_ids);

  return (
    <>
      <h3 className={classes.title}>Детали</h3>

      <div className={classes.content}>
        <dl className={classes.line}>
          <dt className={classes.lineTitle}>Дата релиза:</dt>
          <dd className={classes.lineContent}>
            {formatDate(film.release_date)}
            </dd>
        </dl>
        <dl className={classes.line}>
          <dt className={classes.lineTitle}>Жанр:</dt>
          <dd className={classes.lineContent}>
            {genres.map((genre) => (
              <span className={classes.genre} key={genre.id}>{genre.name}</span>
              ))}
          </dd>
        </dl>
        <dl className={classes.line}>
          <dt className={classes.lineTitle}>Язык оригинала:</dt>
          <dd className={classes.lineContent}>
            {film.original_language}
          </dd>
        </dl>
        <dl className={classes.line}>
          <dt className={classes.lineTitle}>Популярность:</dt>
          <dd className={classes.lineContent}>
            {film.popularity}
          </dd>
        </dl>
        <dl className={classes.line}>
          <dt className={classes.lineTitle}>Ограничения:</dt>
          <dd className={classes.lineContent}>
            {film.adult ? '18+' : 'Нет'}
          </dd>
        </dl>
      </div>
    </>
  );
};

export default FilmContent;
