import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Grid } from '@mui/material';

import { useTypedSelector } from '../../hooks/useTypedSelector';
import storage from '../../modules/localstorage';
import { getFilmsByPage } from '../../modules/pagination';
import { ITEMS_PER_PAGE } from '../../store/parameters';
import { addToBookmarks, addToFavorites, getFilms } from '../../store/slices/filmsSlice';
import FilmCard from '../FilmCard';

const FilmsList: React.FC = () => {
  const dispatch = useDispatch();
  const { page } = useTypedSelector((state) => state.pagination);
  const filters = useTypedSelector((state) => state.filters);
  const { films } = useTypedSelector((state) => state.films);
  const filmsOnPage = getFilmsByPage(films, page, ITEMS_PER_PAGE);
  const authData = useTypedSelector((state) => state.authorization.auth);
  const { isAuthorized } = authData;
  const { bookmarks, favorites } = useTypedSelector((state) => state.films);

  useEffect(() => {
    if (!isAuthorized) return;

    const { bookmarks, favorites } = storage.getFilmsLists() ?? { bookmarks: [], favorites: [] };

    bookmarks.length && dispatch(addToBookmarks(bookmarks));
    favorites.length && dispatch(addToFavorites(favorites));
  }, [isAuthorized]);

  useEffect(() => {
    if (filters.genresFilter && filters.yearFilter && filters.sorting) {
      dispatch(getFilms(filters));
    }
  }, [filters]);

  useEffect(() => {
    if (!isAuthorized) return;

    storage.saveFilmsLists({ favorites, bookmarks });
  }, [bookmarks, favorites]);

  return (
    <Grid container spacing={4}>
      {filmsOnPage.map((film) => (
        <Grid key={film.id} item xs={12} lg={6}>
          <FilmCard
            film={film}
            isAuthorized={isAuthorized}
            bookmarks={bookmarks}
            favorites={favorites}
          />
        </Grid>
      ))}
    </Grid>
  );
};

export default FilmsList;
