import React from 'react';
import { Select, SelectChangeEvent, MenuItem } from '@mui/material';

import { IFiltersSelectOption, IAuthorizedSelectOption } from '../../../types/filters';

interface ISelectProps {
  title: string;
  options: IFiltersSelectOption[] | IAuthorizedSelectOption[];
  value: string;
  disabled: boolean;
  onChange: (value: string) => void;
}

const MySelect: React.FC<ISelectProps> = (props) => {
  const {title, options, value, disabled, onChange} = props;

  const handleChange = (e: SelectChangeEvent): void => {
    onChange(e.target.value);
  };

  return (
    <>
      <p>{title}</p>
      <Select
        value={value}
        size="small"
        sx={{width: "100%"}}
        disabled={disabled}
        onChange={handleChange}
      >
        {options.map((option) => (
          <MenuItem key={option.name} value={option.name}>{option.title}</MenuItem>
        ))}
      </Select>
    </>
  );
};

export default MySelect;
