import {
  DEFAULT_GENRES_FILTER_VALUE,
  DEFAULT_GENRES_SELECT_OPTION,
  DEFAULT_SORTING_OPTION,
  DEFAULT_YEAR_OPTION,
  FILMS_GENRES_FIELD,
  sortingSelectValues,
  yearSelectValues
} from '../store/filters';
import { IFilmGenre } from '../types/films';
import {
  IFilmGenreCheckbox,
  IFiltersSelectOption,
  IFiltersSelectOptionSorting,
  IFiltersState
} from '../types/filters';

export function getSelectOptionByName(
  options: IFiltersSelectOptionSorting[],
  name: string
): IFiltersSelectOptionSorting;

export function getSelectOptionByName(
  options: IFiltersSelectOption[],
  name: string
): IFiltersSelectOption;

export function getSelectOptionByName(
  options: IFiltersSelectOption[] | IFiltersSelectOptionSorting[],
  name: string
) {
  const foundOption = options.find((option) => option.name === name);
  return foundOption ?? options[0];
}

export const getDefaultFiltersState = (): IFiltersState => ({
  sorting: getSelectOptionByName([...sortingSelectValues], DEFAULT_SORTING_OPTION),
  yearFilter: getSelectOptionByName([...yearSelectValues], DEFAULT_YEAR_OPTION),
  genresFilter: DEFAULT_GENRES_FILTER_VALUE
});

export const getDefaultCheckboxesList = (filmsGenres: IFilmGenre[]): IFilmGenreCheckbox[] => {
  return [...filmsGenres].map((genre) => ({
    ...genre,
    checked: DEFAULT_GENRES_FILTER_VALUE.some((genreId) => genreId === genre.id)
  }));
};

export const convertGenresToSelectOptions = (genres: IFilmGenre[]): IFiltersSelectOption[] => (
  [
    DEFAULT_GENRES_SELECT_OPTION,
    ...genres.map((genre) => (
      {
        name: String(genre.id),
        field: FILMS_GENRES_FIELD,
        title: genre.name
      }
    ))
  ]
);
