interface IRadioProps {
  className: string;
  id: string;
  name: string;
  value: string;
  isChecked: boolean;
  onChange: (value: string) => void;
}

const Radio:React.FC<IRadioProps> = (props) => {
  const {
    className,
    id,
    name,
    value,
    isChecked,
    onChange
  } = props;

  const handleRadioChange: React.ChangeEventHandler<HTMLInputElement> = (e) => {
    onChange(e.target.value);
  };

  return (
    <input
      className={className}
      id={id}
      type="radio"
      name={name}
      value={value}
      checked={isChecked}
      onChange={handleRadioChange}
    />
  );
};

export default Radio;
