import { MY_ACCOUNT } from '../store/account-data';
import { IAuthhorizationData, IUserAccount } from '../types/authorization';

export const AUTH_ERROR_MESSAGES = {
  emptyLogin: 'Введите логин',
  emptyPassword: 'Введите пароль',
  wrongData: 'Неверные логин или пароль'
};

export const isAuthorized = (account: IUserAccount): boolean => (
  account.login === MY_ACCOUNT.login
  && account.password === MY_ACCOUNT.password
);

export const getAuthorizedAccount = (account: IUserAccount): IAuthhorizationData => (
  {
    login: account.login,
    password: account.password,
    isAuthorized: true
  }
);

export const validateAuthForm = (account: IUserAccount): string[] => {
  const errorMesages: string[] = [];

  if (!account.login) {
    errorMesages.push(AUTH_ERROR_MESSAGES.emptyLogin);
  }

  if (!account.password) {
    errorMesages.push(AUTH_ERROR_MESSAGES.emptyPassword);
  }

  return errorMesages;
};
