import { IFilmItem } from "../types/films";

const getPagesCount = (itemsCount: number, itemsPerPage: number): number => (
  Math.ceil(itemsCount / itemsPerPage)
);

const getFilmsByPage = (films: IFilmItem[], page: number, filmsPerPageCount: number): IFilmItem[] => {
  return [...films].filter((film, index) => {
    return (index >= (page - 1) * filmsPerPageCount) && (index < page * filmsPerPageCount);
  });
};

export {
  getPagesCount,
  getFilmsByPage
};
