import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { setGenreFilterAction, setSortingAction, setYearFilterAction } from '../store/action-creators/filters';
import { setGenreFilter, setSorting, setYearFilter } from '../store/slices/filtersSlice';
import { FiltersOptionsType } from '../types/filters';


export const useSelectFilters = (filtersOptions: FiltersOptionsType): void => {
  const dispatch = useDispatch();

  const {sortingOption, yearOption, genresFilter} = filtersOptions;

  useEffect(() => {
    dispatch(setSorting(sortingOption));
  }, [sortingOption]);

  useEffect(() => {
    dispatch(setYearFilter(yearOption));
  }, [yearOption]);

  useEffect(() => {
    dispatch(setGenreFilter(genresFilter));
  }, [genresFilter]);
};

