import { createSlice, PayloadAction } from '@reduxjs/toolkit';

import { getDefaultFiltersState, getSelectOptionByName } from '../../modules/filters';
import { IFiltersState } from '../../types/filters';
import { sortingSelectValues, yearSelectValues } from '../filters';

const initialState: IFiltersState = getDefaultFiltersState();

const filtersSlice = createSlice({
  name: 'filters',
  initialState,
  reducers: {
    setSorting(state, action: PayloadAction<string>) {
      state.sorting = getSelectOptionByName([...sortingSelectValues], action.payload);
    },
    setYearFilter(state, action: PayloadAction<string>) {
      state.yearFilter = getSelectOptionByName([...yearSelectValues], action.payload);
    },
    setGenreFilter(state, action: PayloadAction<number[]>) {
      state.genresFilter = action.payload;
    }
  }
});

export const {
  setSorting,
  setYearFilter,
  setGenreFilter
} = filtersSlice.actions;

export default filtersSlice.reducer;
