import React from 'react';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { IconButton, Stack, Typography } from '@mui/material';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import BookmarkBorderIcon from '@mui/icons-material/BookmarkBorder';

import { getPosterUrl } from '../../modules/films';
import { IFilmItem } from '../../types/films';
import {
  addToBookmarks,
  addToFavorites,
  deleteFromBookmarks,
  deleteFromFavorites
} from '../../store/slices/filmsSlice';
import { modalOpen } from '../../store/slices/modalSlice';

import classes from './index.module.scss';

interface IPropsFilmCard {
  film: IFilmItem;
  isAuthorized: boolean;
  bookmarks: number[],
  favorites: number[]
}

const FilmCard: React.FC<IPropsFilmCard> = (props) => {
  const {
    film,
    isAuthorized,
    bookmarks,
    favorites
  } = props;
  const filmId = film.id;
  const dispatch = useDispatch();

  const isInFavorites = favorites.includes(filmId);
  const isInBookmarks = bookmarks.includes(filmId);

  const handleFavoritesClick = (): void => {
    if (!isAuthorized) {
      dispatch(modalOpen());
      return;
    }

    if (isInFavorites) {
      dispatch(deleteFromFavorites(filmId));
      return;
    }

    dispatch(addToFavorites([filmId]));
  };

  const handleBookmarksClick = (): void => {
    if (!isAuthorized) {
      dispatch(modalOpen());
      return;
    }

    if (isInBookmarks) {
      dispatch(deleteFromBookmarks(filmId));
      return;
    }

    dispatch(addToBookmarks([filmId]));
  };

  const addToFavoritesClasses: string[] = isInFavorites
  ? [classes.filmInfoFavourites, classes.isAdded]
  : [classes.filmInfoFavourites];

  const addToBookmarksClasses: string[] = isInBookmarks
  ? [classes.filmInfoBookmarks, classes.isAdded]
  : [classes.filmInfoBookmarks];

  return (
    <article className={classes.filmCard}>
      <div className={classes.filmCover}>
        <img
          src={`${getPosterUrl( film.poster_path || film.backdrop_path)}`}
          alt={`${film.title} - постер загружается только при включенном VPN`}
        />
      </div>

      <Stack direction="column" className={classes.filmInfo}>
        <Stack direction="row" className={classes.filmInfoTop}>
          <span className={classes.filmInfoRating}>
            Рейтинг: {film.vote_average}
          </span>
          <IconButton
            className={addToFavoritesClasses.join(' ')}
            onClick={handleFavoritesClick}
          >
            <StarBorderIcon />
          </IconButton>
          <IconButton
            className={addToBookmarksClasses.join(' ')}
            onClick={handleBookmarksClick}
          >
            <BookmarkBorderIcon />
          </IconButton>
        </Stack>

        <div className={classes.filmInfoMain}>
          <Typography
            variant="h6"
            component="h3"
          >
            {film.title}
          </Typography>
        </div>

        <div className={classes.filmInfoBottom}>
          <Link className={classes.filmInfoLink} to={`/film/${film.id}`}>
            Подробнее
          </Link>
        </div>
      </Stack>
    </article>
  );
};

export default FilmCard;
