import { useEffect, useState } from 'react';
import FoundFilm from '../../components/FoundFilm';

import SearchForm from '../../components/SeacrhForm';
import { searchFilms } from '../../modules/films';
import { filmsItems } from '../../store/films-items';
import {
  DEFAULT_GENRES_SELECT_OPTION,
  DEFAULT_POPULARITY_VALUE,
  DEFAULT_SCORE_VALUE
} from '../../store/filters';

import classes from './index.module.scss';

const DEFAULT_FILM_NUMBER = 0;

const Search: React.FC = () => {
  const [genresOption, setGenresOption] = useState(DEFAULT_GENRES_SELECT_OPTION.name);
  const [scoreValue, setScoreValue] = useState(DEFAULT_SCORE_VALUE);
  const [popularityValue, setPopularityValue] = useState(DEFAULT_POPULARITY_VALUE);
  const [filmNumber, setFilmNumber] = useState(DEFAULT_FILM_NUMBER);

  const genreId = genresOption ? Number(genresOption) : null;
  const films = searchFilms(filmsItems, {genreId, scoreValue, popularityValue});
  const filmsCount = films.length;
  const foundFilm = films.length ? films[filmNumber] : null;
  const foundFilmId = foundFilm ? foundFilm.id : null

  useEffect(() => {
    setFilmNumber(DEFAULT_FILM_NUMBER);
  }, [filmsCount]);

  const getNextFilm = (): void => {
    if (filmNumber < filmsCount - 1) {
      setFilmNumber(filmNumber + 1);
    } else {
      setFilmNumber(DEFAULT_FILM_NUMBER);
    }
  };

  console.log('films > ', films);

  return (
    <div className={classes.pageWrapper}>
      <section className={classes.filters}>
        <h2 className={classes.filtersTitle}>Найти фильм:</h2>
        <SearchForm
          genresOption={genresOption}
          setGenresOption={setGenresOption}
          scoreValue={scoreValue}
          setScoreValue={setScoreValue}
          popularityValue={popularityValue}
          setPopularityValue={setPopularityValue}
          getNextFilm={getNextFilm}
          foundFilmId={foundFilmId}
        />
      </section>

      <section className={classes.filmDescription}>
        <FoundFilm
          film={films[filmNumber] ? films[filmNumber] : null}
        />
      </section>
    </div>
  );
};

export default Search;
